@import url("https://fonts.googleapis.com/css?family=Lato:400,700");

:root {
  --primaryColor: #2c23f8;
  --mainWhite: #fff;
  --mainBlack: #222;
  --mainGrey: #ececec;
  --mainSpacing: 0.1rem;
  --mainTransition: all 0.3s linear;
  --mainRadius: 0.5rem;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: var(--mainBlack);
  background: var(--mainWhite);
  font-family: "Lato", sans-serif;
}

.navbar {
  background: var(--mainWhite);
  position: sticky;
  top: 0;
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  background: rgb(231, 226, 221);
  z-index: 1;
  .navbar-center {
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1.5rem;
    .nav-icon {
    font-size: 1.5rem;
    }
    .logo {
    width: 80px;
    height: 40px;
    }
    .cart-btn {
    position: relative;
    cursor: pointer;
    }
    .cart-items {
    position: absolute;
    top: -8px;
    right: -8px;
    background: var(--primaryColor);
    padding: 0 5px;
    border-radius: 30%;
    color: var(--mainWhite);
    }
  }
}

.hero {
  min-height: calc(100vh - 60px);
  background: url("../images/hero-bcg.jpeg") center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  .banner {
    text-align: center;
    background: rgba(255, 255, 255, 0.8);
    border-radius: var(--mainRadius);
    display: inline-block;
    padding: 2rem;
    .banner-title {
      font-size: 3.4rem;
      text-transform: uppercase;
      letter-spacing: var(--mainSpacing);
      margin-bottom: 3rem;
    }
    .banner-btn {
      padding: 1rem 3rem;
      text-transform: uppercase;
      letter-spacing: var(--mainSpacing);
      font-size: 1rem;
      background: var(--primaryColor);
      border-radius: var(--mainRadius);
      color: var(--mainBlack);
      border: 1px solid var(--primaryColor);
      transition: var(--mainTransition);
      cursor: pointer;
      &:hover {
        background: transparent;
        color: var(--primaryColor);
      }
    }
  }
}